@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/opensans.woff2") format("woff2"), url("../../fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../../fonts/opensansbold.woff2") format("woff2"), url("../../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
body {
  display: flex;
  flex-direction: column;
  margin: 0;
  min-height: 100%;
  font-family: "Open Sans", "Arial", sans-serif;
  color: #000;
  font-size: 15px;
  font-style: normal;
  font-weight: 700;
  line-height: 24px;
}

html {
  height: 100%;
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

a {
  text-decoration: none;
}

.container {
  padding-left: 30px;
  padding-right: 30px;
}
@media screen and (min-width: 768px) {
  .container {
    padding-left: 60px;
    padding-right: 60px;
  }
}

.main-header {
  background-color: transparent;
  position: relative;
}

.main-header__wrapper {
  display: flex;
  padding: 22px 33px;
  position: relative;
  height: 79px;
}
@media screen and (min-width: 768px) {
  .main-header__wrapper {
    padding: 0;
  }
}

.main-header__nav {
  position: absolute;
  width: 100%;
  z-index: 3;
  display: none;
  left: 0;
  top: 0;
}
@media screen and (min-width: 768px) {
  .main-header__nav {
    position: static;
    display: block;
    background-color: transparent;
  }
}

.main-header__logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 3;
}
@media screen and (min-width: 768px) {
  .main-header__logo {
    left: 60px;
    transform: translate(0, -50%);
  }
}

.main-header__button {
  display: block;
  background-image: url("../img/stack.svg#menu-open");
  width: 20px;
  height: 14px;
  border: none;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  margin-left: auto;
  z-index: 5;
  position: absolute;
  right: 30px;
  top: 50%;
  transform: translateY(-50%);
}
.main-header__button:hover {
  opacity: 0.6;
}
.main-header__button:active {
  opacity: 0.3;
}
@media screen and (min-width: 768px) {
  .main-header__button {
    display: none;
  }
}

.main-header__logo-image {
  max-width: none;
}

.main-header__toggle-closed {
  background-image: url("../img/icons/menu-close.svg");
  width: 22px;
  height: 22px;
  border: none;
  background-color: var(--blue);
  background-repeat: no-repeat;
  background-position: center;
}

.main-nav {
  position: relative;
}

.main-nav__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
.main-nav__list--desktop {
  display: none;
}

.main-nav__item {
  position: relative;
  padding: 26px 26px 26px 76px;
  text-align: left;
  background-color: #ffffff;
  box-shadow: 0px 1px 0px 0px #ececec, 0px 1px 0px 0px #ececec inset;
}

.main-nav__item-link {
  position: relative;
  color: #000000;
  font-family: inherit;
  font-size: 17px;
  font-style: normal;
  font-weight: 700;
  line-height: 24px;
  text-decoration: none;
}
.main-nav__item-link::before {
  content: "";
  position: absolute;
  width: 18px;
  height: 18px;
  background-repeat: no-repeat;
  background-position: center;
  top: 50%;
  left: -44px;
  transform: translateY(-50%);
}
.main-nav__item-link--search::before {
  background-image: url("../img/stack.svg#search");
}
.main-nav__item-link--basket::before {
  background-image: url("../img/stack.svg#basket");
  width: 21px;
}

.menu-nav_opened {
  display: block;
}